@import url(https://fonts.googleapis.com/css2?family=Lato&family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&display=swap);
body {
  height: 100vh;
  width: 100vw;
  margin: 0;
  box-sizing: border-box;
  
  background-color: #141414;
  font-family: 'Libre Baskerville', serif;
}

body #root{
  height: 100%;
  width: 100%;
}
.quote{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 66%;
}

.text{
  text-align: left;

  font-size: clamp(16px, 6vw, 48px);
  font-weight: 300;
  color: #ffffff;
  text-shadow: 4px 4px 2px #383838;
}

.text span{
  font-weight: bold;
}

.author{
  text-align: right;

  font-size: clamp(4px, 4vw, 28px);
  color: #ffffff;
  text-shadow: 4px 4px 2px rgba(255, 56, 139, 0.80);
}

@media (min-width: 416px){
  .text{
    font-size: clamp(16px, 8vw, 72px);
  }
  .author{
    font-size: clamp(4px, 6vw, 44px);
  }
}
.interaction{
  display: flex;;
  flex-direction: row;
  justify-content: center;

  margin: 24px;

  width: 80%;
  height: 48px;
  
}

.interaction .a:visited{
  
}
.tweet {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  padding: 12px;
  border-radius: 16px 0 0 16px;
  color: #1DA1F2;
  background-color: white;
  font-weight: bold;
  text-decoration: none;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
}

.tweet img{
  width: 24px;
  height: 24px;
  color: #1DA1F2
}

.next-quote{
  padding: 12px;
  border-radius: 0 16px 16px 0;
  border: none;
  background-color: #373737;
  color: white;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: bold
}

.next-quote:focus{
  outline: none;
}

.next-quote:hover{
  background-color: #777;
}

.next-quote:active{
  color: #141414;
}

