}
.box{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  height: 80%;

  border-radius: 16px;
  background: linear-gradient(31deg, rgba(255,56,138,0.66) 0%, rgba(0,170,255,0.66) 100%);
}
.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  background-image: url(/freeCodeCamp-Frontend/static/media/streets-japan-min.01bc2808.jpg);
  background-size: cover;
}
